#wrapper-white-line {
    .modal {
        position: absolute;
        display: none;
        opacity: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 9995;

        .overlay-modal {
            position: absolute;
            width: 100%;
            height: 100%;
        }

        .box-content-modal {
            position: absolute;
            width: 90%;
            top: 85px;
            bottom: 40px;
            left: 50%;
            padding: 30px 55px;
            box-sizing: border-box;
            transform: translateX(-50%);
            background-color: rgba(0,0,0,.73);
        }

        .btn-close-modal {
            position: absolute;
            width: 32px;
            height: 32px;
            text-indent: -9999px;
            top: 25px;
            right: 30px;
            border: 1px solid #fff;
            border-radius: 50%;
            z-index: 2;

            span {
                position: absolute;
                display: block;
                width: 12px;
                height: 14px;
                top: 50%;
                left: 50%;
                margin: -7px 0 0 -6px;
                background: url(../images/icon-close.png) no-repeat center center;
                background-size: contain;
            }
        }

        .content-modal {
            position: relative;
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            padding-right: 30px;
            overflow-y: auto;
            overflow-x: hidden;
        }

        .item-modal {
            display: none;
            #h .group();
        }

        .box-video {
            position: relative;
        }

        .cover-mp4 {
            position: absolute;
            display: none;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            z-index: 2;

            .btn-play {
                width: 60px;
                height: 60px;
                margin: -30px 0 0 -30px;
                border-width: 2px;

                span {
                    width: 28px;
                    height: 32px;
                    margin-left: 4px;
                }
            }

            .embed-container {
                z-index: 1;
            }
        }

        .modal-default-video {
            .box-video {
                margin-top: 60px;
            }

            .links-video {
                float: right;
                margin-top: 18px;

                li {
                    float: left;
                    margin-left: 10px;
                }

                .btn-default {
                    width: 100px;
                    height: 26px;
                    font-size: 11px;
                    font-family: @bold;
                    line-height: 24px;
                }
            }
        }

        .modal-default-internal {
            padding:20px 60px 0 10px;

            .links-video {
                float: right;
                margin-top: 18px;

                li {
                    float: left;
                    margin-right: 10px;
                }

                .btn-default {
                    width: 100px;
                    height: 26px;
                    font-size: 11px;
                    font-family: @bold;
                    line-height: 24px;
                }
            }
        }

        .internal-content-modal {
            color: #fff;
            padding-bottom: 30px;

            .slider-content-item {
                padding: 0 20px;
            }

            h3 {
                font-size: 28px;
                font-family: @light;
                text-transform: uppercase;
            }

            h4 {
                position: relative;
                font-size: 43px;
                font-family: @bold;
                letter-spacing: -2px;
                padding-top: 20px;
                margin-bottom: 10px;
                text-transform: uppercase;

                &:before {
                    position: absolute;
                    content: "";
                    width: 36px;
                    height: 10px;
                    top: 0;
                    left: 0;
                    background-color: #fff;
                }
            }

            .box-title-modal {
                position: relative;
                font-family: @light;
                text-transform: uppercase;

                h3 {
                    display: inline-block;
                    max-width: 85%;
                    margin-bottom: 5px;
                    letter-spacing: -2px;
                }

                p {
                    font-size: 20px;
                }

                .graphics-title {
                    position: absolute;
                    display: block;
                    height: 26px;
                    top: 4px;
                    right: 0;
                    transform: skewX(-20deg);
                    background: url(../images/graphics-title.png) no-repeat center center;
                }
            }

            .text-category {
                position: absolute;
                top: -2px;
                left: 50px;
                font-size: 12px;
                color: #51a6ef;
                font-family: @light;
                text-transform: uppercase;
            }

            .internal-box-content-modal {
                #h .group();
                clear:fix;
                position: relative;
                display: block;
                padding-top: 20px;
                margin-top: 20px;
                border-top: 1px solid #fff;

                &.content-zoom-line .slider-zoom .slick-arrow {
                    margin-top: -25px;
                }
            }

            .box-text-video {
                padding-bottom: 40px;
                .text {
                    position: relative;
                    float: left;
                    font-size: 15px;
                    text-transform: uppercase;
                }

                .thumb-video {
                    position: relative;
                    float: right;
                    width: 412px;
                    height: 214px;

                    img {
                        display: block;
                        width: 100%;
                    }

                    a {
                        position: absolute;
                        display: block;
                        width: 100%;
                        height: 100%;
                        top: 0;
                        left: 0;
                        background-position: center center;
                        background-size: cover;
                    }

                    .btn-play {
                        width: 60px;
                        height: 60px;
                        margin: -30px 0 0 -30px;
                        border-width: 2px;

                        span {
                            width: 32px;
                            height: 38px;
                            margin-left: 5px;
                        }
                    }
                }

                .links-video {
                    position: absolute;
                    float: none;
                    bottom: 40px;
                    left: 0;
                }
            }

            .slider-item {
                position: relative;
                padding: 20px 7px 0;

                b {
                    position: absolute;
                    top: 0;
                    left: 10px;
                    font-size: 12px;
                    color: #51a6ef;
                    font-family: @light;
                    text-transform: uppercase;
                }

                img {
                    display: block;
                    width: 100%;
                }

                .thumb-gallery {
                    position: relative;
                    display: block;
                    width: 100%;
                    height: 100%;
                }
            }

            .slick-arrow {
                position: absolute;
                width: 12px;
                height: 26px;
                top: 50%;
                text-indent: -9999px;
                margin-top: 10px;
                transform: translateY(-50%);
                transition: opacity .2s ease;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: contain;
                z-index: 2;
            }

            .slick-prev {
                left: -20px;
                background-image: url(../images/arrow-left.png);
            }

            .slick-next {
                right: -20px;
                background-image: url(../images/arrow-right.png);
            }

            .slick-disabled {
                opacity: .2;
            }

            .slider-thumb-videos {
                .thumb-gallery {
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: cover;
                }

                .btn-play {
                    width: 54px;
                    height: 54px;
                    border-width: 2px;
                    margin: -27px 0 0 -27px;

                    span {
                        width: 28px;
                        height: 32px;
                        margin-left: 4px;
                    }
                }
            }

            .slider-thumb-photos {
                .slider-item {
                    padding: 0 7px;
                }

                .slick-arrow {
                    margin-top: 0;
                }
            }

            .box-description {
                padding-top: 40px;
                padding-bottom: 20px;
                text-transform: uppercase;

                h4 {
                    font-size: 28px;
                    color: #5ba3da;
                    font-family: @light;
                    margin-bottom: 20px;
                    padding: 0;

                    &:before {
                        display: none;
                    }
                }

                ul {
                    font-size: 22px;
                    font-family: @light;
                }
            }

            .box-zoom {
                .btn-back {
                    position: absolute;
                    width: 18px;
                    height: 18px;
                    top: 10px;
                    right: 0;
                    background: url(../images/icon-close.png) no-repeat center center;
                    background-size: contain;
                }

                h5 {
                    position: relative;
                    max-width: 750px;
                    font-size: 12px;
                    text-transform: uppercase;
                    color: #51a6ef;
                    margin: 0 auto 10px;
                }

                .content-box-zoom {
                    max-width: 95%;
                }

                .box-video {
                    margin-left: 15px;
                }

                .links-video {
                    position: relative;
                    float: none;
                    max-width: 750px;
                    margin: 18px auto 0;
                    text-align: right;

                    li {
                        float: none;
                        display: inline-block;
                        margin-right: 0;
                        margin-left: 10px;
                    }
                }

                .slider-zoom {
                    width: 750px;
                    //max-height: 474px;
                    margin: 0 auto;

                    img {
                        width: 100%;
                    }

                    .slick-arrow {
                        width: 25px;
                        height: 55px;
                    }

                    .slick-prev {
                        left: -35px;
                    }

                    .slick-next {
                        right: -35px;
                    }

                    .box-video {
                        margin-left: 0;
                    }
                }
            }
        }
    }
}

/*------------------------------------
    Desktop
--------------------------------------*/
.desktop #wrapper-white-line {
    .modal .btn-close-modal {
        overflow: hidden;
        transition: all .4s ease;

        &:hover {
            background-color: rgba(255,255,255,0.1);
        }
    }
}

/*------------------------------------
    Mac e mobile
--------------------------------------*/
.mac {
    #wrapper-white-line {
        .modal .modal-default-video .links-video .btn-default {
            line-height: 24px;
        }
    }
}

.mobile {
    #wrapper-white-line {
        .box-scroll-modal {
            display: none !important;
        }
    }
}

@media only screen and (min-width: 1024px) and (max-height: 750px) {
    #wrapper-white-line {
        .modal {
            .box-content-modal {
                padding: 30px 65px;
            }

            .modal-default-video {
                position: relative;
                margin: 0 auto;
                width: 700px;

                .box-video {
                    margin: 0;
                }
            }
        }
    }
}

@media only screen and (max-width: 1199px) {
    #wrapper-white-line {
        .modal {
            .box-content-modal {
                width: 90%;
                height: 80%;
            }

            .modal-default-video .box-video {
                margin-top: 45px;
            }

            .internal-content-modal {
                h4 {
                    margin-bottom: 5px;
                }

                .box-title-modal h3 {
                    max-width: 95%;
                }

                .box-text-video {
                    padding-bottom: 0;

                    .text {
                        float: none;
                        width: 100%;
                    }

                    .thumb-video {
                        float: none;
                        display: block;
                        margin-top: 10px;
                    }

                    .links-video {
                        position: relative;
                        display: block;
                        bottom: inherit;
                        margin-top: 12px;
                    }
                }

                .box-zoom {
                    h5 {
                        position: relative;
                        display: block;
                        width: 95%;
                        max-width: 750px;
                        margin: 0 auto  10px;
                    }

                    .slider-zoom {
                        width: 95%;
                        max-width: 750px;
                        margin: 0 auto;
                    }

                    .slider-zoom + .links-video {
                        position: relative;
                        float: none;
                        width: 95%;
                        max-width: 750px;
                        margin: 15px auto 0;
                        text-align: right;

                        li {
                            float: none;
                            display: inline-block;
                        }
                    }
                }

                .slider-item b {
                    font-size: 10px;
                }
            }
        }
    }
}

@media only screen and (max-width: 1023px) {
    #wrapper-white-line {
        .modal {
            .modal-default-internal {
                padding: 20px 0px;

                .box-title-modal h3 {
                    max-width: 80%;
                }
            }

            .internal-content-modal .slider-thumb-videos .slider-item {
                padding-top: 30px;
            }
        }
    }
}

@media only screen and (max-width: 767px) {
    #wrapper-white-line {
        .modal {
            z-index: 9999;

            .btn-close-modal {
                top: 10px;
                right: 10px;
            }

            .box-content-modal {
                width: 100%;
                height: 100%;
                top: 0;
                bottom: 0;
                padding: 56px 10px;
                background-color: #000;
            }

            .content-modal {
                padding-right: 0;
            }

            .modal-default-video {
                position: relative;

                .links-video {
                    position: relative;
                    float: none;
                    width: 220px;
                    margin: 20px auto 0;

                    li {
                        margin: 5px;
                    }
                }
            }

            .modal-default-internal {
                padding-top: 0;
            }

            .internal-content-modal {
                .slider-content-item {
                    padding: 0 10px;
                }

                h3 {
                    font-size: 17px;
                }

                h4 {
                    font-size: 24px;
                    padding-top: 12px;

                    &:before {
                        width: 20px;
                        height: 7px;
                    }
                }

                .box-title-modal {
                    p {
                        font-size: 11px;
                    }

                    .graphics-title {
                        height: 17px;
                        background-size: auto 17px;
                    }
                }

                .text-category {
                    font-size: 9px;
                    left: 26px;
                }

                .links-video .btn-default {
                    width: 90px;
                    font-size: 10px;
                }

                .slick-prev {
                    left: -10px;
                }

                .slick-next {
                    right: -10px;
                }

                .box-text-video {
                    .text {
                        font-size: 11px;
                    }

                    .links-video {
                        margin-left: -5px;

                        li {
                            margin-right: 0;
                            margin-left: 5px;
                        }
                    }

                    .thumb-video {
                        width: auto;
                        max-width: 412px;
                        height: auto;
                        margin-top: 15px;

                        .btn-play {
                            width: 50px;
                            height: 50px;

                            span {
                                width: 25px;
                                height: 28px;
                                margin-left: 4px;
                            }
                        }
                    }
                }

                .box-description {
                    h4 {
                        font-size: 22px;
                    }

                    ul {
                        font-size: 20px;
                    }
                }

                .box-zoom .slider-zoom {
                    .slick-arrow {
                        width: 18px;
                        height: 40px;
                    }

                    .slick-prev {
                        left: 5px;
                        background-image: url(../images/arrow-left-shadow.png);
                    }

                    .slick-next {
                        right: 5px;
                        background-image: url(../images/arrow-right-shadow.png);
                    }
                }

                .box-zoom .slider-zoom + .links-video {
                    padding-top: 60px;

                    li {
                        margin-left: 4px;

                        &:first-child {
                            position: absolute;
                            top: 0;
                            right: 0;
                        }

                        .btn-default {
                            width: 87px;
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 640px) {
}

@media only screen and (max-width: 479px) {
    #wrapper-white-line {
        .modal {
            .modal-default-video {
                position: relative;
                top: 50%;
                transform: translateY(-50%);

                .links-video {
                    position: relative;
                    float: none;
                    width: 220px;
                    margin: 80px auto 0;

                    li {
                        margin: 5px;
                    }
                }
            }

            .internal-content-modal {
                .box-title-modal h3 {
                    max-width: 82%;
                }

                .box-zoom {
                    .slider-zoom, h5 {
                        width: 100%;
                    }
                }

                .box-zoom .slider-zoom + .links-video {
                    width: 100%;

                    li {
                        margin-left: 3px;
                    }
                }

                .slider-thumb-videos .slider-item {
                    padding-top: 20px;
                }
            }
        }
    }
}
