.box-scroll-modal {
    position: absolute;
    top: 77px;
    bottom: 30px;
    right: 35px;
    width: 26px;
    background: url('../images/line-scroll.png') no-repeat center 0;

    &:before, &:after {
        position: absolute;
        content: "";
        width: 100%;
        height: 11px;
        left: 0;
        background-repeat: no-repeat;
        background-position: center center;
    }

    &:before {
        top: -2px;
        background-image: url(../images/scroll-arrow-top.png);
    }

    &:after {
        bottom: -2px;
        background-image: url(../images/scroll-arrow-bottom.png);
    }
}

.scroll-container {
    position: absolute;
    width: 100%;
    top: 20px;
    bottom: 20px;
    left: 0;
    overflow: hidden;
}
.scroll-bar {
    position: absolute;
    width: 5px;
    height: 20px;
    top: 0;
    left: 50%;
    margin-left: -2px;
    background-color: #fff;
}

