@charset "utf-8";

@import 'vendor/_normalize';
@import 'vendor/_reset';
@import 'vendor/_slick';
@import 'vendor/_photo-sphere-viewer';
@import '_fonts';
@import '_variables';
@import '_mixins';
@import '_scroll';
@import '_preloader';
@import '_modal';

* {
   -moz-osx-font-smoothing: grayscale;
   -webkit-font-smoothing: antialiased;
   outline: solid 0;
}

.header-example {
    width:100%;
    height:70px;
    background-color:#ccc;
}

#wrapper-white-line {
    position: relative;
    max-width: 1200px;
    font-family: @regular;
    margin: 0 auto;
    overflow: hidden;

    h1, h2, h3, h4, h5, h6, strong, b {
        font-weight: normal;
    }

    .center {
        #h .group();
        position: relative;
        width: 1060px;
        margin: 0 auto;
    }

    [data-ani-type] {
       visibility: hidden;

       &[data-animated="true"] {
           visibility: visible;
       }

       @media (max-width: 1023px) {
           visibility: visible;
       }
    }

    .embed-container {
        position: relative;
        padding-bottom: 56.25%;
        height: 0;
        overflow: hidden;
        max-width: 100%;

        iframe, object, embed, video {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    .btn-more {
        display: block;
        width: 45px;
        height: 45px;
        text-indent: -9999px;
        border-radius: 50%;
        background-color: #2bdbfa;

        &:before {
            position: absolute;
            content: "";
            width: 26px;
            height: 26px;
            top: 50%;
            left: 50%;
            margin: -13px 0 0 -13px;
            background: url(../images/icon-more.png) no-repeat center center;
            background-size: contain;
        }
    }

    .btn-default {
        display: block;
        width: 150px;
        height: 38px;
        color: #fff;
        text-align: center;
        line-height: 36px;
        text-transform: uppercase;
        box-sizing: border-box;
        border: 1px solid #fff;

        span {
            position: relative;
            width: 100%;
            height: 100%;
        }
    }

    .btn-play {
        position: absolute;
        display: block;
        width: 88px;
        height: 88px;
        top: 50%;
        left: 50%;
        border: 3px solid #fff;
        border-radius: 50%;
        margin: -44px 0 0 -39px;

        span {
            position: absolute;
            display: block;
            width: 45px;
            height: 52px;
            top: 50%;
            left: 50%;
            margin-left: 8px;
            transform: translate(-50%,-50%);
        }

        svg {
            fill: #fff;
            width: 100%;
            height: 100%;
        }
    }

    .seal-180 {
        position: absolute;
        width: 56px;
        height: 56px;
        top: 80px;
        left: 50%;
        text-indent: -9999px;
        transform: translateX(-50%);
        background: url(../images/180.png) no-repeat center center;
        background-size: contain;
        z-index: 2;
    }

    .text-180-icon {
        position: absolute;
        width: 630px;
        top: 50%;
        left: 50%;
        font-size: 19px;
        text-align: center;
        color: #fff;
        font-family: @cond-regular;
        text-transform: uppercase;
        padding: 14px 0;
        border: 1px solid #fff;
        transform: translate(-50%,-50%);
        background-color: rgba(0,0,0,.24);
        z-index: 2;
    }

    /*------------------------------------
        Header
    --------------------------------------*/
    #header {
        position: absolute;
        width: 100%;
        height: 54px;
        top: 0;
        left: 0;
        z-index: 9996;
        background: url(../images/bg-header.jpg) no-repeat center center;

        .center {
            width: 920px;
            height: 100%;
        }

        .social-media {
            float: left;
            margin-top: 13px;

            li {
                float: left;
                margin-right: 15px;

                a {
                    display: block;
                    height: 28px;

                    span {
                        display: block;
                        width: 100%;
                        height: 100%;
                    }

                    svg {
                        display: block;
                        width: 100%;
                        height: 100%;
                        fill: #fff;
                    }
                }

                .btn-facebook {
                    width: 26px;
                }

                .btn-instagram {
                    width: 28px;
                }

                .btn-twitter {
                    width: 32px;
                }

                .btn-pinterest {
                    width: 28px;
                }
            }
        }

        .menu-white-line {
            position: absolute;
            width: 420px;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);

            ul {
                #h .group();
            }

            li {
                float: left;
                margin: 0 10px;

                &.active {
                    .btn-default {
                        background-color: rgba(0,0,0,0.3);
                    }
                }
            }

            .btn-default {
                width: 120px;
                height: 30px;
                font-size: 14px;
                line-height: 28px;
            }
        }

        .logo-repense {
            float: right;
            width: 112px;
            height: 23px;
            margin-top: 15px;
            text-indent: -9999px;
            background: url(../images/logo-repense.png) no-repeat center center;
            background-size: contain;
        }
    }

    /*------------------------------------
        Main
    --------------------------------------*/
    main {
        .slider-pages {
            width: 100%;
            height: calc(~"100vh - 70px");

            .slick-list, .slick-track, .item-pages {
                height: 100%;
            }
        }

        section {
            position: relative;
            height: 100%;

            .bg-section {
                position: absolute;
                display: block;
                width: 100%;
                top: 54px;
                bottom: 0;
                left: 0;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
            }
        }

        #home {
            overflow: hidden;

            .bg-section {
                //transition: all .4s ease;
                background-position: center top;
                background-image: url(../images/bg-home.jpg);
            }

            .center {
                width: 1166px;
                height: 100%;
            }

            .box-text {
                transition: opacity .4s ease;
                position: absolute;
                top: 50%;
                left: 20px;
                color: #fff;
                text-transform: uppercase;
                opacity: .76;
                transform: translateY(-50%);

                h1 {
                    position: relative;
                    display: block;
                    font-size: 86px;
                    line-height: 81px;
                    letter-spacing: -6px;
                    padding: 60px 0 0 0;
                    margin-left: 15px;
                    font-family: @bold;

                    &:before {
                        position: absolute;
                        content: "";
                        width: 74px;
                        height: 18px;
                        top: 0;
                        left: 5px;
                        background-color: #fff;
                    }
                }

                .text {
                    max-width: 492px;
                    font-size: 23px;
                    line-height: 28px;
                    border-top: 4px dotted #fff;
                    padding: 46px 22px;
                    margin-top: 46px;
                    box-sizing: border-box;
                }
            }

            .btn-share {
                margin-left: 22px;
            }
        }
    }
}

/*------------------------------------
    Desktop
--------------------------------------*/
.desktop #wrapper-white-line {
    .btn-play {
        transition: background-color .4s ease;

        span {
            transition: all .4s ease;

            svg {
                transition: all .4s ease;
            }
        }

        &:hover {
            background-color: rgba(0,0,0,0.2);

            span {
                transform: translate(-50%,-50%) scale(0.8);
                margin-left: 6px;
            }
        }
    }

    .btn-default {
        position: relative;
        overflow: hidden;
        transition: background-color .4s ease;

        span {
            position: relative;
            z-index: 2;
        }

        &:hover {
            background-color: rgba(0,0,0,0.2);
        }
    }

    #header .social-media li a {
        span {
            transition: all .4s ease;
        }

        &:hover {
            span {
                transform: scale(1.05);
                opacity: .8;
            }
        }
    }

    main {
        &.content-blur {
            .slider-pages {
                filter: blur(8px);
                transform: scale(1.04);
            }

            #home {
                .box-text {
                    opacity: 0;
                }

                .btn-play {
                    opacity: 0;
                }
            }
        }
    }
}

/*------------------------------------
    Mac e mobile
--------------------------------------*/
.mac {
    #wrapper-white-line {
        .btn-default {
            line-height: 36px;
        }
    }
}

.mobile {
    #wrapper-white-line {
        [data-ani-type] {
           visibility: visible;
        }

        .btn-default {
            line-height: 34px;
        }
    }
}

@media only screen and (min-width: 1024px) and (max-height: 750px) {
    #wrapper-white-line {
        .btn-play {
            margin: -44px 0 0 -44px;
        }

        .slider-pages {
            min-height: 580px;
        }

        main {
            #home {
                min-height: 580px;

                .box-text {
                    margin-top: 27px;

                    h1 {
                        font-size: 70px;
                        line-height: 70px;
                        padding-top: 30px;
                    }

                    .text {
                        font-size: 20px;
                        line-height: 24px;
                        margin-top: 36px;
                        padding: 30px 20px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1199px) {
    #wrapper-white-line {
        width: 100%;

        .center {
            width: 100%;
            padding: 0 20px;
            box-sizing: border-box;
        }

        .btn-play {
            margin: -44px 0 0 -34px;
        }

        main {
            #home {
                .center {
                    width: 100%;
                }

                .box-text {
                    margin-top: 15px;

                    h1 {
                        font-size: 80px;
                        line-height: 78px;
                        padding-top: 50px;
                    }

                    .text {
                        font-size: 20px;
                        line-height: 24px;
                        margin-top: 36px;
                        padding: 30px 20px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1023px) {
    #wrapper-white-line {
        #header .center {
            width: 100%;
        }

        main {
            #home {
                .box-text .text {
                    margin-top: 220px;
                }

                .btn-play {
                    margin-top: 13px;
                }
            }
        }
    }
}

@media only screen and (max-width: 767px) {
    #wrapper-white-line {
        .center {
            padding: 0 15px;
        }


        .seal-180 {
            width: 40px;
            height: 40px;
            top: 90px;
        }

        .text-180-icon {
            width: 296px;
            top: inherit;
            bottom: 20px;
            font-size: 12px;
            transform: translate(-50%, 0);
        }

        #header {
            height: 85px;
            background-size: cover;

            .social-media, .logo-repense {
                margin-top: 10px;
            }

            .logo-repense {
                width: 100px;
                height: 21px;
            }

            .social-media li {
                a {
                    height: 24px;
                }
            }

            .menu-white-line {
                width: 294px;
                top: inherit;
                bottom: 10px;
                margin-left: 0;
                transform: translateX(-50%);

                li {
                    margin: 0 4px;
                }

                .btn-default {
                    width: 90px;
                    height: 26px;
                    line-height: 24px;
                    font-size: 12px;
                }
            }
        }

        main {
            .slider-pages {
                min-height: 524px;
            }

            section {
                .bg-section {
                    top: 85px;
                }
            }
            #home {
                .center {
                    padding: 0 15px;
                }

                .box-text {
                    margin-top: 40px;

                    h1 {
                        font-size: 35px;
                        line-height: 35px;
                        padding: 15px 0 0 0;
                        letter-spacing: -2px;
                        margin-left: 4px;

                        &:before {
                            width: 27px;
                            height: 7px;
                            left: 3px;
                        }
                    }

                    .text {
                        max-width: 180px;
                        font-size: 13px;
                        line-height: 16px;
                        margin-top: 110px;
                        padding: 20px 0;
                        border-width: 2px;

                        br {
                            display: none;
                        }
                    }
                }

                .btn-share {
                    margin-left: 0;
                }

                .btn-play {
                    width: 60px;
                    height: 60px;
                    margin: -14px 0 0 -34px;
                    border-width: 2px;

                    span {
                        width: 30px;
                        height: 36px;
                        margin-left: 4px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 640px) {
    .header-example {
        height: 44px;
    }

    #wrapper-white-line {
        main {
            .slider-pages {
                height: calc(~"100vh - 44px");
            }
        }
    }
}

@media only screen and (max-width: 479px) {
    #wrapper-white-line {
        .center {
            padding: 0 15px;
        }
    }
}
